#emptycart {
  color: white;
  font-size: 10vh;
  text-align: left;
  margin: 5vh 2vw;
  font-weight: 100;
}

#checkout
{
  background-color: rgb(238, 238, 238);
  width:25vw;
  min-height: 70vh;
  margin: auto;
  margin-top: 5vh;
  padding: 1%;
  border-radius: 2px;
  font-family: monospace;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;

}
.checkoutitemcard
{
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin: 5% 5%;
  font-size:larger;
  font-weight: 100;
  border-bottom: 1px solid rgb(48, 48, 48);
  padding-bottom: 2%;


}
.checkoutitemcard button
{
  border-radius: 10%;
  border: 1px solid ;
  background-color: rgb(65, 65, 65);
  color: white;
  cursor: pointer;
  
}
.checkoutitemcard img
{
  height: 10vh;
  width: 15%;
  object-fit: cover;
  border-radius: 5px;
}
.itemname
{
  width: 50%;
  
}
.itemquantity
{
  width: 20%;
  display:flex;
  align-items: center;
  justify-content: space-around;
}
.itemprice
{
  width: 15%;
  font-style: italic;
}