@import url("https://fonts.googleapis.com/css2?family=Nixie+One&display=swap");

#Products {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  margin-top: 10vh;
}
.productcard {
  height: 60vh;
  /* width: 20vw; */
  margin: 5vh 3vw;
  background-color: black;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
  cursor: pointer;
  transition: transform 0.2s;
}
.productcard:hover
{
  transform: scale(1.05);
}
.productcard * {
  margin: 0;
}
.productcard img {
  width: 100%;
  height: 80%;
  display: block;
  margin: 0 auto;
  object-fit: cover;
}
.productdetails {
  max-width: 100%;
  background-color: #2f2f2f;
  height: 20%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
  font-family: "Nixie One", cursive;
  font-weight: lighter;
}
.productname {
  margin-left: 2vw;
  background-color: rgb(228, 228, 228);
  color: black;
  padding: 2%;
  padding-right: 10%;
  font-weight: 500;
}
.productprice {
  margin-right: 2vw;
}
.addbutton {
  height: 10%;
  background-color: #2f2f2f;
  /* text-align: left; */
}
.addbutton button {
  margin: auto;
  border: none;
  margin-left: 2vw;
  /* border-radius: 50%; */
  background-color: rgb(228, 228, 228);
  color: black;
  font-family: "Nixie One", cursive;
  height: 90%;
  width:30%
}
